<template>
  <v-card class="pa-4">
    <h2>Closed Assignments by Clerk (By Date)</h2>
    <v-row dense>
      <v-col cols="12" md="3">
        <v-select
          v-model="selectedRole"
          :items="roleOptions"
          label="User Role"
          dense
          required
        />
      </v-col>
      <v-col cols="12" md="3">
        <v-select
          v-model="selectedBranch"
          :items="filteredBranches"
          item-text="name"
          item-value="id"
          label="Branch"
          dense
          required
          :disabled="
            selectedRole === 'clerk' ||
            selectedRole === 'manager' ||
            !selectedRole
          "
        />
      </v-col>
      <v-col cols="12" md="3">
        <v-select
          v-model="selectedClerk"
          :items="filteredClerks"
          item-text="username"
          item-value="id"
          label="Clerk"
          dense
          required
          :disabled="
            (selectedRole === 'branchclerk' ||
              selectedRole === 'branchmanager') &&
            !selectedBranch
          "
        />
      </v-col>
      <v-col cols="12" md="3">
        <v-menu v-model="menuStart" :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="startDate"
              label="Start Date"
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
              dense
              required
            />
          </template>
          <v-date-picker
            v-model="startDate"
            type="date"
            @change="menuStart = false"
          />
        </v-menu>
        <v-menu v-model="menuEnd" :close-on-content-click="false">
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="endDate"
              label="End Date"
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
              dense
              required
            />
          </template>
          <v-date-picker
            v-model="endDate"
            type="date"
            @change="menuEnd = false"
          />
        </v-menu>
      </v-col>
    </v-row>
    <v-divider class="my-4" />
    <v-btn
      color="success"
      class="mt-2"
      :disabled="!selectedClerk || !startDate || !endDate"
      @click="exportExcel"
      :loading="exporting"
    >
      <v-icon left>mdi-file-excel</v-icon>
      Export Closed Assignments
    </v-btn>
    <v-alert type="info" outlined class="mt-4">
      ðŸš§ This feature generates a styled Excel sheet. Table preview coming soon!
    </v-alert>
  </v-card>
</template>
<script>
import MemberDataService from "../../services/MemberDataService";
import BranchDataService from "../../services/BranchDataService";
import { apiConfig } from "../../config/api.config";

export default {
  name: "ClosedByClerkDate",
  data() {
    return {
      roleOptions: [
        { text: "CLERK", value: "clerk" },
        { text: "MANAGER", value: "manager" },
        { text: "BRANCHCLERK", value: "branchclerk" },
        { text: "BRANCHMANAGER", value: "branchmanager" },
      ],
      selectedRole: null,
      branches: [],
      selectedBranch: null,
      clerks: [],
      selectedClerk: null,
      startDate: null,
      endDate: null,
      menuStart: false,
      menuEnd: false,
      exporting: false,
    };
  },
  computed: {
    filteredBranches() {
      if (!this.selectedRole) return [];
      if (this.selectedRole === "clerk" || this.selectedRole === "manager") {
        // Only HQKL branch, but will be disabled
        return this.branches.filter((b) => b.name === "HQKL");
      }
      // All branches except HQKL
      return this.branches.filter((b) => b.name !== "HQKL");
    },
    filteredClerks() {
      if (!this.selectedRole) return [];
      if (this.selectedRole === "clerk" || this.selectedRole === "manager") {
        // Show all clerks/managers, branch independent
        return this.clerks
          .filter(
            (clerk) =>
              clerk.active &&
              clerk.roles &&
              (clerk.roles.some((b) => b.roleCode === "clerk") ||
                clerk.roles.some((b) => b.roleCode === "manager"))
          )
          .sort((a, b) => a.username.localeCompare(b.username));
      }
      // For branchclerk/branchmanager, filter by branch
      if (!this.selectedBranch) return [];
      const branchObj = this.branches.find((b) => b.id === this.selectedBranch);
      if (!branchObj) return [];
      const branchCode = branchObj.brCode;
      return this.clerks
        .filter(
          (clerk) =>
            clerk.active &&
            clerk.roles &&
            clerk.roles.some((b) => b.roleCode === this.selectedRole) &&
            Array.isArray(clerk.branches) &&
            clerk.branches.some((b) => b.brCode === branchCode)
        )
        .sort((a, b) => a.username.localeCompare(b.username));
    },
  },
  mounted() {
    this.loadBranches();
    this.retrieveClerks();
  },
  watch: {
    selectedRole(newRole) {
      if (newRole === "clerk" || newRole === "manager") {
        // Disable branch selection for HQ roles
        this.selectedBranch = null;
      } else {
        this.selectedBranch = null;
      }
      this.selectedClerk = null;
    },
    selectedBranch() {
      this.selectedClerk = null;
    },
  },
  methods: {
    async loadBranches() {
      const res = await BranchDataService.getAll();
      this.branches = (res.data || []).sort((a, b) =>
        a.name.localeCompare(b.name)
      );
    },
    retrieveClerks() {
      MemberDataService.getAll()
        .then((response) => {
          this.clerks = response.data
            .filter(
              (item) =>
                item.roles &&
                item.roles.some(
                  (b) =>
                    b.roleCode === "clerk" ||
                    b.roleCode === "manager" ||
                    b.roleCode === "branchclerk" ||
                    b.roleCode === "branchmanager"
                ) &&
                item.active
            )
            .sort((a, b) => a.username.localeCompare(b.username));
        })
        .catch((e) => {
          console.log(e);
        });
    },
    async exportExcel() {
      this.exporting = true;
      try {
        const params = {
          clerkId: this.selectedClerk,
          startDate: this.startDate,
          endDate: this.endDate,
        };
        const url =
          `${apiConfig.baseURL}/compliance/closed-by-clerk/export?` +
          new URLSearchParams(params).toString();
        window.open(url, "_blank");
      } catch (err) {
        this.$emit("error", err.message || "Failed to export Excel");
      } finally {
        this.exporting = false;
      }
    },
  },
};
</script>
